import React, { Component } from 'react';

import ArticleSimple from "./articles/simple.md";
import customTerraform from "./articles/terraformProviders.md";

import {Link} from "react-router-dom";
import {Route} from "react-router";
import {Blogpost} from "./Blogpost";

const articleMap = {
    "custom-terraform-providers": {
        title: "Custom Terraforming",
        summary: "Making your own terraform providers",
        location: customTerraform,
        tags: ["simple", "start"],
    },
    "simple-article": {
        title: "Simple Blogpost",
        summary: "A simple article to help test the process...",
        location: ArticleSimple,
        tags: ["simple", "start"],
    },
};

export class ArticleSummary extends Component {
    render() {
        return (
            <div className={"articleSummaryContainer"}>
                <div className={"articleSummaryTitle"}>
                    <h1><Link to={this.props.url}>
                        {this.props.title}</Link>
                    </h1>
                </div>
                <div className={"articleSummarySummary"}>
                    <p>{this.props.summary}</p>
                </div>
            </div>
        )
    }
}

const Blog = ({match}) => {
    let blogpostname = match.params.blogpostname;
    console.log(blogpostname);
    let matchedPost = articleMap[blogpostname];
    console.log(matchedPost.location);
    return <Blogpost articleSource={matchedPost.location}/>
};

export class Articles extends Component {
    constructor(props, context) {
        super(props, context);
        this.articleMap = articleMap;
        this.state = {
            isLoading: true,
        };
    }

    render() {
        let articleSummaries =[];
        let i = 0;

        for(const a in this.articleMap){
            const article = this.articleMap[a];
            articleSummaries.push(<ArticleSummary key={i}
                                        title={article.title}
                                        summary={article.summary}
                                        tags={article.tags}
                                        url={"/blog/" + a}
                        />);
            i++;
        }

        return (
            <div className={"articlesList"}>
                <Route path={"/blog"}
                       exact={true}
                       component={()=>articleSummaries}/>
                <Route path={"/blog/:blogpostname"}
                       component={Blog}
                />
                {/*{articleRoutes}*/}
                {/*<svg viewBox="0 0 50 50">*/}
                {/*    <circle className="ring" cx="25" cy="25" r="20"></circle>*/}
                {/*    <circle className="ball" cx="25" cy="5" r="3.5"></circle>*/}
                {/*</svg>*/}
            </div>
        )
    }
}

