import React, { Component } from 'react';
import ReactMarkdown from "react-markdown";
import {Link} from "react-router-dom";

// Blogpost is a blog post
export class Blogpost extends Component {
    constructor(props, context) {
        super(props, context);
        this.articleSource = this.props.articleSource;
        this.state = {
            content: '',
        }
    }

    componentWillMount() {
        if(this.articleSource.startsWith("/static")) {
            fetch(this.articleSource).then(response => response.text())
                .then((resp) => {
                    this.setState({content: resp})
                })
                .catch((err) => {
                    console.log("Failed", err)
                })
        }
    }

    render() {
        return <div className={"pageContainer"}>
                <Link to={"/blog"}><span>Back</span></Link>
                <div className={"article"}>
                    <ReactMarkdown source={this.state.content} />
                </div>

            </div>
    }
}